:root {
    --dark-main: #672280;
    --light_main: #A626D3;
    --dark-pale-main: #75637c;
    --pale-main: #c4bdc6;
    --garient: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
}

* {
    box-sizing: border-box;
    margin: 0;
    font-family: "Karla", sans-serif;
    color: white;
}

body {
    margin: 32px;
    background: var(--pale-main);
}

.gradient {
    background: var(--garient);
}

#root {
    display: flex;
    align-items: center;
    justify-content: center;
}

.container {
    width: 550px;
    background: #eeeeee;
    border-radius: 16px;
    box-shadow: 0 0 32px var(--dark-pale-main);
    overflow: hidden;
}

nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
}

.branding {
    display: flex;
    gap: 8px;
    letter-spacing: -0.1em;
}

.logo {
    height: 20px;
}

.main-content {
    padding: 32px;
}

.meme-input {
    margin-bottom: 32px;
}

.input-field-container {
    display: flex;
    gap: 16px;
}

.input-field {
    padding: 10px;
    width: 100%;;
    color: var(--dark-main);
    border: 1px solid var(--dark-main);
    border-radius: 4px;
}

.input-field::placeholder {
    color: var(--light-main);
}

.input-button {
    width: 100%;
    margin: 16px 0 0 0;
    padding: 10px;
    font-family: "Karla", sans-serif;
    font-size: 16px;
    border-radius: 4px;
    border: 1px solid var(--dark-main);
    letter-spacing: -0.1em;
    cursor: pointer;
}

.meme-output {
    position: relative;
    text-align: center;
}

.meme-image {
    width: 100%;
    border-radius: 4px;
}

.meme-text {
    position: absolute;
    width: 100%;
    text-transform: uppercase;
    font-family: 'Impact';
    font-size: 32px;
    text-shadow:1px 1px 0 #000,
    -1px 1px 0 #000,
    1px -1px 0 #000,
    -1px -1px 0 #000,
    0px 1px 0 #000,
    0px -1px 0 #000,
    -1px 0px 0 #000,
    1px 0px 0 #000,
    2px 2px 0 #000,
    -2px 2px 0 #000,
    2px -2px 0 #000,
    -2px -2px 0 #000,
    0px 2px 0 #000,
    0px -2px 0 #000,
    -2px 0px 0 #000,
    2px 0px 0 #000,
    1px 2px 0 #000,
    -1px 2px 0 #000,
    1px -2px 0 #000,
    -1px -2px 0 #000,
    2px 1px 0 #000,
    -2px 1px 0 #000,
    2px -1px 0 #000,
    -2px -1px 0 #000;
}

.meme-text-top {
    top: 16px;
}

.meme-text-bottom {
    bottom: 16px;
}